"use client";

import { useState } from "react";
import {
  Search,
  Plus,
  Users,
  Star,
  MessageCircle,
  Filter,
  ChevronDown,
  MoreHorizontal,
  CheckCircle2,
  Eye,
} from "lucide-react";
import { Card, CardContent } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Avatar, AvatarFallback, AvatarImage } from "@/components/ui/avatar";
import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuTrigger,
} from "@/components/ui/dropdown-menu";

interface CreatorsTabProps {
  workspaceSlug: string;
}

interface Creator {
  id: string;
  name: string;
  username: string;
  avatar: string | null;
  verified: boolean;
  rating: number;
  completedCampaigns: number;
  totalViews: number;
  status: "active" | "pending" | "inactive";
}

const statusColors = {
  active: "bg-emerald-500/10 text-emerald-500",
  pending: "bg-amber-500/10 text-amber-500",
  inactive: "bg-muted text-muted-foreground",
};

export function CreatorsTab({ workspaceSlug }: CreatorsTabProps) {
  const [searchQuery, setSearchQuery] = useState("");
  const [creators] = useState<Creator[]>([]);
  const [filterStatus, setFilterStatus] = useState<string>("all");

  const filteredCreators = creators.filter((creator) => {
    const matchesSearch =
      creator.name.toLowerCase().includes(searchQuery.toLowerCase()) ||
      creator.username.toLowerCase().includes(searchQuery.toLowerCase());
    const matchesStatus = filterStatus === "all" || creator.status === filterStatus;
    return matchesSearch && matchesStatus;
  });

  return (
    <div className="space-y-6 max-w-6xl">
      {/* Header */}
      <div className="flex flex-col sm:flex-row sm:items-end sm:justify-between gap-4">
        <div>
          <h1 className="text-2xl font-bold tracking-tight">Creators</h1>
          <p className="text-muted-foreground text-sm mt-1">
            Manage your creator network
          </p>
        </div>
        <Button className="gap-2">
          <Plus className="w-4 h-4" />
          Invite Creator
        </Button>
      </div>

      {/* Search and Filters */}
      <div className="flex flex-col sm:flex-row gap-3">
        <div className="relative flex-1">
          <Search className="absolute left-3 top-1/2 -translate-y-1/2 w-4 h-4 text-muted-foreground" />
          <input
            type="text"
            placeholder="Search creators..."
            className="w-full h-11 pl-10 pr-4 rounded-xl bg-muted border-0 text-sm placeholder:text-muted-foreground focus:outline-none focus:ring-2 focus:ring-primary/50 transition-shadow"
            value={searchQuery}
            onChange={(e) => setSearchQuery(e.target.value)}
          />
        </div>
        <DropdownMenu>
          <DropdownMenuTrigger asChild>
            <Button variant="outline" className="gap-2 h-11 rounded-xl">
              <Filter className="w-4 h-4" />
              <span className="capitalize">{filterStatus}</span>
              <ChevronDown className="w-3 h-3" />
            </Button>
          </DropdownMenuTrigger>
          <DropdownMenuContent align="end">
            {["all", "active", "pending", "inactive"].map((status) => (
              <DropdownMenuItem
                key={status}
                onClick={() => setFilterStatus(status)}
                className={filterStatus === status ? "bg-primary/10 text-primary" : ""}
              >
                <span className="capitalize">{status}</span>
              </DropdownMenuItem>
            ))}
          </DropdownMenuContent>
        </DropdownMenu>
      </div>

      {/* Creators Grid */}
      {filteredCreators.length > 0 ? (
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
          {filteredCreators.map((creator) => (
            <Card key={creator.id} className="bg-card border-border hover:border-primary/40 transition-colors cursor-pointer">
              <CardContent className="p-5">
                <div className="flex items-start justify-between mb-4">
                  <div className="flex items-center gap-3">
                    <Avatar className="w-12 h-12">
                      <AvatarImage src={creator.avatar || undefined} />
                      <AvatarFallback className="bg-muted text-muted-foreground">
                        {creator.name.split(" ").map((n) => n[0]).join("")}
                      </AvatarFallback>
                    </Avatar>
                    <div>
                      <div className="flex items-center gap-1.5">
                        <h3 className="font-semibold">{creator.name}</h3>
                        {creator.verified && (
                          <CheckCircle2 className="w-4 h-4 text-primary fill-primary/20" />
                        )}
                      </div>
                      <p className="text-sm text-muted-foreground">@{creator.username}</p>
                    </div>
                  </div>
                  <DropdownMenu>
                    <DropdownMenuTrigger asChild>
                      <Button variant="ghost" size="sm" className="w-8 h-8 p-0">
                        <MoreHorizontal className="w-4 h-4" />
                      </Button>
                    </DropdownMenuTrigger>
                    <DropdownMenuContent align="end">
                      <DropdownMenuItem>View Profile</DropdownMenuItem>
                      <DropdownMenuItem>Send Message</DropdownMenuItem>
                      <DropdownMenuItem>Invite to Campaign</DropdownMenuItem>
                      <DropdownMenuItem className="text-destructive">Remove</DropdownMenuItem>
                    </DropdownMenuContent>
                  </DropdownMenu>
                </div>

                <div className="flex items-center gap-2 mb-4">
                  <span className={`px-2 py-0.5 rounded-full text-[10px] font-medium ${statusColors[creator.status]}`}>
                    {creator.status}
                  </span>
                  <span className="flex items-center gap-1 text-xs text-muted-foreground">
                    <Star className="w-3.5 h-3.5 text-amber-400 fill-amber-400" />
                    {creator.rating.toFixed(1)}
                  </span>
                </div>

                <div className="flex items-center gap-4 pt-4 border-t border-border text-xs text-muted-foreground">
                  <span className="flex items-center gap-1">
                    <CheckCircle2 className="w-3.5 h-3.5" />
                    {creator.completedCampaigns} campaigns
                  </span>
                  <span className="flex items-center gap-1">
                    <Eye className="w-3.5 h-3.5" />
                    {creator.totalViews.toLocaleString()} views
                  </span>
                </div>

                <div className="flex gap-2 mt-4">
                  <Button variant="outline" size="sm" className="flex-1 text-xs">
                    <MessageCircle className="w-3.5 h-3.5 mr-1.5" />
                    Message
                  </Button>
                  <Button size="sm" className="flex-1 text-xs">
                    Invite
                  </Button>
                </div>
              </CardContent>
            </Card>
          ))}
        </div>
      ) : (
        <Card className="bg-card border-border">
          <CardContent className="py-16">
            <div className="text-center">
              <div className="w-16 h-16 rounded-full bg-muted flex items-center justify-center mx-auto mb-4">
                <Users className="w-8 h-8 text-muted-foreground" />
              </div>
              <h3 className="text-lg font-semibold mb-2">No creators yet</h3>
              <p className="text-muted-foreground text-sm mb-6 max-w-sm mx-auto">
                Start building your creator network to run successful campaigns
              </p>
              <Button className="gap-2">
                <Plus className="w-4 h-4" />
                Find Creators
              </Button>
            </div>
          </CardContent>
        </Card>
      )}
    </div>
  );
}
